import { Component } from '@angular/core';
import { AppState } from '@imunify/nav-root';
import { AgentStatus } from '@imunify360-api/misc';

@Component({
  selector: 'i360-standalone-warning',
  template: `
      <div class="flexer warning-container">
          <i360-warning-message>
           <h3>{{ 'warning.stand-alone.title' | translate }}</h3>
              <ng-container [ngSwitch]="appState.imunifyStatus|async">
                  <span *ngSwitchCase="AgentStatus.NOT_INSTALLED">{{  'warning.stand-alone.error-description' | translate | translateCut: 0 }}
                      <a [href]="docLink" class="i360-dashboard-link" target="_blank">
                        {{ 'warning.stand-alone.error-description' | translate | translateCut: 1 }}</a>
                  </span>
                  <span *ngSwitchCase="AgentStatus.SOCKET_INACCESSIBLE">{{ 'warning.stand-alone.socketError' | translate }}</span>
              </ng-container>
          </i360-warning-message>
      </div>`,
  styles: [`
        .warning-container {
            flex-wrap: wrap;
            justify-content: space-evenly;
        }
  `],

})
export class StandaloneWarningComponent {
    AgentStatus = AgentStatus;
    readonly docLink = 'https://docs.imunify360.com/control_panel_integration/#there-are-four-' +
        'main-steps-in-general-required-for-having-imunify360-stand-alone-running-on-your-server';

    constructor(public appState: AppState) {
    }
}
